.footer_outer_container{
    background-color: #111;
    color: grey;
    display:flex;
    justify-content: center;
}
.footer_inner_container{
    width: 60%;
    /* background-color: red; */
}
.footer_icons{
    /* background-color: blue; */
    width: 12%;
    display: flex;
    justify-content: space-between;
    margin-left: 10px;
    color: #fff;
}
.footer_data{
    display: flex;
    justify-content: space-between;
}
.footer_data ul{
    list-style: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 12vh;
    /* background-color: blue; */

}
.footer_data ul li{
    cursor: pointer;
}
.service_code{
display: flex;
align-items: center;
justify-content: center;
border: #fff 1px solid;
width: 10%;
margin: 10px 0;
cursor: pointer;
}
.copy-write{
    font-size: 12px;
    margin-bottom:10px ;
}

@media (max-width:900px){
    .footer_inner_container{
        width: 75%;
    }
    .footer_icons {
        width: 16%;
    }
    .service_code {
        width: 16%;
    }
}
@media (max-width: 768px){
    .footer_inner_container{
        width: 95%;
    }

    }